body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

.navbar-light .navbar-nav .nav-link.active {
  color: #2196f3;
}

.slogan {
  margin: 0;
  padding: 8px;
}

.filter-nav {
  text-align: left;
  margin: 3% 0 3% 3.25%;
}

.title {
  margin-bottom: 2%;
}

.checkbox {
  margin-right: 3%;
}

.filter-descr {
  margin-bottom: 10%;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

/* Hide default HTML checkbox */
.switch input {
  display: none;
}

/* The slider */
.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  -webkit-transition: 0.4s;
  transition: 0.4s;
}

input.default:checked + .slider {
  background-color: #444;
}
input.primary:checked + .slider {
  background-color: #2196f3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196f3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 34px;
}

.slider.round:before {
  border-radius: 50%;
}

label {
  margin-bottom: 0;
}

.list-group-item {
  text-align: center;
  position: relative;
  display: inline-grid;
  padding: 0 2% 0 0;
  margin-bottom: -1px;
  background-color: #fff;
  border: none;
}

img {
  width: 80%;
}

.image-infos  {
  width: 80%;
  border: 2px solid black;
}

.image-name {
  margin: 2% 0 0% 0;
  font-size: 1.75rem;
}

.description {
  font-style: italic;
}

.pagination {
  margin-top: 3%;
  display: flex;
  padding-left: 0;
  list-style: none;
  border-radius: 0.25rem;
}

